define('skylark-formio/templates/bootstrap/iconClass',[],function () {
    'use strict';
    return (iconset, name, spinning) => {
        if (iconset === 'fa') {
            switch (name) {
            case 'save':
                name = 'download';
                break;
            case 'zoom-in':
                name = 'search-plus';
                break;
            case 'zoom-out':
                name = 'search-minus';
                break;
            case 'question-sign':
                name = 'question-circle';
                break;
            case 'remove-circle':
                name = 'times-circle-o';
                break;
            case 'new-window':
                name = 'window-restore';
                break;
            case 'move':
                name = 'arrows';
                break;
            case 'time':
                name = 'clock-o';
                break;
            }
        }
        return spinning ? `${ iconset } ${ iconset }-${ name } ${ iconset }-spin` : `${ iconset } ${ iconset }-${ name }`;
    };
});
